import React from "react"
import { Link, graphql } from "gatsby"
import Img from "gatsby-image"

import Layout from "../components/layout"
import SEO from "../components/seo"

const Badge = ({
  id,
  title,
  score,
  percentile,
  dateCompleted,
  level,
  localImage,
}) => (
  <Link to={id}>
    <div className="col-12 col-sm-6 col-lg-4 col-xl-3">
      <h1>{title}</h1>
      
      <div>{score}</div>
      <div>{Math.floor(percentile)}%</div>
      <div>{dateCompleted}</div>
      <div>{level}</div>
      <Img
        fluid={localImage.childImageSharp.fluid}
        alt={title}
        style={{ float: "left", marginRight: "1rem", width: 150 }}
      />

    </div>
  </Link>
)

const Skills = ({ data }) => {
  const badges = data.allPluralsightJson.edges.map(({ node }) => (
    <Badge key={node.dateCompleted} {...node} />
  ))
  return (
    <Layout>
      <SEO title="Building My Blog" />
      <div className="row">
        <h1 className="col-12">Building My Blog</h1>
        <div className="col-12">
          <p>
            Trying out Gatsbyjs because I can. Here are some of my skill assessments
            from pluralsight
          </p>
        </div>
        <div className="col-12">
          {badges}
        </div>
      </div>
    </Layout>
  )
}

export default Skills

export const query = graphql`
  query {
    allPluralsightJson {
      edges {
        node {
          id
          percentile
          title
          dateCompleted(fromNow: false)
          level
          localImage {
            childImageSharp {
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
          score
        }
      }
    }
  }
`
